//breadcrumb
.main-breadcrumb {
	padding: calc(10rem + 85px) 0 10rem;
	position: relative;
	z-index: 1;
	background-size: cover;
	background-attachment: scroll;
	background-position: center center;

	&::before {
		content: "";
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		background-image: url(../images/bgs/breadcrumb_overlay_dots.png);
		background-size: cover;
		background-position: center;
		opacity: .2;
		z-index: -1;
	}

	&::after {
		content: "";
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		background: $main_color;
		opacity: .31;
		z-index: -1;
	}

	.breadcrumb-title {
		text-align: center;
		margin-bottom: 15px;

		h2 {
			font-size: 60px;
			text-transform: uppercase;
			font-weight: 800;
			color: #fff;
			margin-bottom: 0;
			position: relative;
			display: inline-block;
			line-height: 62px;

			&::before,
			&::after {
				content: "";
				position: absolute;
				left: 0;
				height: 1px;
				width: 100%;
				border-radius: 50%;
				background-color: rgb(227, 160, 55, 0.5);
			}

			&::before {
				top: 0;
				bottom: auto;
			}

			&::after {
				top: auto;
				bottom: 0;
			}
		}
	}

	.breadcrumb {
		justify-content: center;

		.breadcrumb-item {
			font-size: 16px;
			text-transform: uppercase;
			font-weight: 600;

			a {
				color: #fff;
			}

			&.active {
				color: #fff;
				font-weight: 500;
			}

			&::before {
				color: #ffffff;
			}
		}
	}
}

//breadcrumb

//side widget
.side-widget {
	padding-inline-start: 25px;

	.other-services,
	.other-features {
		margin-bottom: 25px;

		.title {
			h3 {
				font-size: 20px;
				margin-bottom: 0;
				color: #fff;
				padding: 20px 25px;
				background-color: $second_color;
				text-transform: capitalize;
				font-weight: 600;
			}
		}

		ul {
			padding: 0;
			margin-bottom: 0;
			border: 4px solid #ebebeb;
			border-top: none;

			li {
				list-style: none;
				line-height: 1.8em;

				&:last-child {
					padding-bottom: 0;
					border-bottom: 0;
				}

				&:hover,
				&.active {
					a {
						color: $second_color;

						&::before {
							opacity: 1;
						}
					}
				}

				a {
					display: block;
					padding: 18px 20px;
					color: $main_color;
					font-size: 16px;
					font-weight: 500;
					line-height: 1;
					border-bottom: 1px solid #ebebeb;
					position: relative;
					text-transform: capitalize;

					&::before {
						content: "";
						position: absolute;
						left: -4px;
						height: 100%;
						top: 0;
						background: $second_color;
						width: 4px;
						transition: .3s linear;
						opacity: 0;
					}
				}
			}
		}
	}

	.need-support {
		background-position: center;
		background-repeat: no-repeat;
		background-size: cover;
		position: relative;
		z-index: 1;
		text-align: center;
		padding: 50px 40px;

		&::after {
			content: "";
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			background: $second_color;
			opacity: .9;
			z-index: -1;
		}

		.title {
			margin-bottom: 25px;

			h3 {
				color: #fff;
				font-size: 26px;
				font-weight: 600;
				text-transform: capitalize;
			}

			p {
				font-size: 16px;
			}
		}

		p {
			color: #fff;
			line-height: 1.7em;
			font-size: 14px;
			margin-bottom: 25px;
			@extend %max_lines;
			-webkit-line-clamp: 5;
		}

		.btn {
			@extend %btn_selector;
			background: $main_color none repeat scroll 0 0;

			&:hover {
				color: $dark_color;
			}

			&::before {
				background-color: #fff;
			}
		}
	}
}

