//about page
section.about-in-page {
	padding: $padding_section;
	background-color: #FAFAFA;

	.content {
		.nav {
			margin-inline-end: 30px;

			.nav-link {
				background: #fff;
				box-shadow: 0 0 70px 0 rgb(45 3 91 / 5%);
				position: relative;
				z-index: 9;
				color: $main_color;
				@include borderRadius(0);
				margin-bottom: 20px;
				font-size: 18px;
				text-transform: capitalize;
				text-align: initial;
				padding: 20px;
				font-weight: 600;
				display: flex;
				align-items: center;

				span {
					@extend %max_one_line;
				}

				i {
					margin-inline-end: 10px;
					color: $second_color;
					font-size: 26px;
				}

				&.active {
					&::before {
						content: "";
						position: absolute;
						top: 50%;
						transform: translateY(-50%);
						width: 0;
						height: 0;
						border-style: solid;
						border-width: 12px 0 12px 12px;
						border-color: transparent transparent transparent #fff;
						right: -12px;
						box-shadow: 0 0 70px 0 rgb(45 3 91 / 5%);
					}
				}
			}
		}

		.tab-content {
			box-shadow: 0 0 70px 0 rgb(45 3 91 / 5%);
			background: #fff;
			padding: 30px;

			.about-content {
				.image {
					margin-bottom: 15px;
					max-height: 350px;
					overflow: hidden;

					img {
						@extend %fitimage;
					}
				}

				p {
					font-size: 16px;
					color: $gray;
					line-height: 24px;
				}

				h3 {
					font-weight: 600;
					color: $gray;
					font-size: 18px;
					margin-bottom: 8px;
				}

				ul {
					padding: 0;

					li {
						position: relative;
						margin-bottom: 10px;
						color: $gray;
						list-style: none;
						display: flex;
						align-items: baseline;

						&::before {
							content: "";
							background-color: $second_color;
							width: 7px;
							min-width: 7px;
							height: 7px;
							display: inline-block;
							margin-inline-end: 10px;
							@include borderRadius(50%)
						}
					}
				}
			}
		}

		.service-box {
			padding-inline-start: 0
		}
	}
}

