//services page
section.services-in-page {
	padding: $padding_section;
	background-color: #FAFAFA;

	.content {
		.single-services {
			box-shadow: 0 0 43px 0 rgb(40 11 72 / 11%);
			background: #fff;
			overflow: hidden;
			margin-bottom: 25px;

			&:hover {
				.services-content {
					border-color: $second_color;

					&::before {
						opacity: 1;
					}
				}
			}

			.services-thumb {
				height: 300px;

				img {
					@extend %fitimage;
				}
			}

			.services-content {
				padding: 0 30px 35px;
				border-top: 4px solid transparent;
				transition: .3s linear;
				position: relative;
				z-index: 1;

				&::before {
					content: "";
					position: absolute;
					height: 125px;
					width: 125px;
					background: $second_color;
					border-radius: 50%;
					left: 25px;
					top: -44px;
					transition: .3s linear;
					z-index: -2;
					opacity: 0;
				}

				&::after {
					content: "";
					position: absolute;
					left: 25px;
					top: 0;
					height: 85px;
					width: 130px;
					background: #fff;
					z-index: -2;
				}

				.services-icon {
					height: 115px;
					width: 115px;
					text-align: center;
					line-height: 82px;
					background: #fff;
					border-radius: 50%;
					font-size: 40px;
					color: $second_color;
					position: relative;
					margin-bottom: -30px;
					z-index: -1;
					margin-top: -40px;
				}

				h3 {
					font-size: 20px;
					margin-bottom: 15px;
					color: $main_color;
					transition: all ease 0.3s;
					@extend %max_lines;
					-webkit-line-clamp: 1;

					&:hover {
						color: $second_color;
					}
				}

				p {
					font-size: 15px;
					font-weight: normal;
					color: #545454;
					margin-bottom: 0;
					@extend %max_lines;
					-webkit-line-clamp: 2;
				}
			}
		}
	}
}

