//requst quote
section.request-quote {
	padding: $padding_section;

	.content {
		.submit-success {
			text-align: center;

			.success-icon {
				margin-bottom: 30px;

				i {
					height: 100px;
					width: 100px;
					display: flex;
					align-items: center;
					justify-content: center;
					border: 1px dashed $second_color;
					border-radius: 50%;
					background: #f8f8f8;
					font-size: 32px;
					color: $second_color;
					margin: 0 auto 25px;
				}
			}

			h3 {
				text-transform: capitalize;
				color: $second_color;
			}
		}

		.stepwizard {
			display: table;
			width: 100%;
			position: relative;
			margin-bottom: 70px;

			.stepwizard-step {
				display: table-cell;
				text-align: center;
				position: relative;
				width: calc(100% / 2);

				button[disabled] {
					opacity: 1 !important;
					filter: alpha(opacity=100) !important;
				}

				p {
					font-size: 14px;
					font-weight: 700;
					text-transform: uppercase;
					color: #514959;
					line-height: 1;
					margin-bottom: 0;
				}

				.btn-rounded {
					height: 100px;
					width: 100px;
					display: flex;
					align-items: center;
					justify-content: center;
					border: 1px dashed $second_color;
					border-radius: 50%;
					background: #f2f3f7;
					font-size: 32px;
					color: #918c97;
					margin: 0 auto 25px;

					&.active {
						background-color: $second_color;
						color: #fff;
					}
				}
			}
		}

		.stepwizard-row {
			display: table-row;

			&::before {
				content: "";
				top: 35%;
				bottom: 0;
				position: absolute;
				width: 100%;
				height: 1px;
				border-bottom: 1px dashed $second_color;
				z-index: 0;
			}
		}

		.stepwizard-form {
			.form-group {
				margin-bottom: 20px;

				label {
					display: block;
					margin-bottom: 15px;
					font-weight: 600;
					text-transform: capitalize;
					font-size: 14px;
				}

				.form-control,
				.form-select {
					display: block;
					width: 100%;
					line-height: 28px;
					padding: 12px 15px;
					color: #222222;
					font-size: 14px;
					font-weight: 400;
					border-radius: 0;
					background-color: #f8f8f8;
					border-color: #f8f8f8;

					&:focus {
						border-color: $second_color;
						box-shadow: none;
					}
				}

				textarea {
					height: auto !important;
				}
			}

			.stepwizard-btn {
				.btn {
					@extend %btn_selector;
				}
			}
		}
	}
}

