//compliance policy
section.compliance-policy {
	padding: 80px 0 120px;
	background-color: #fff;
	position: relative;
	z-index: 0;

	.content {
		display: flex;
		background-image: url(../images/bgs/pattern-30.png);
		position: relative;
		justify-content: center;
		background-size: cover;
		max-width: 880px;
		margin: auto;

		&::before,
		&::after {
			position: absolute;
			content: '';
			bottom: -20px;
			opacity: 0.20;
			background-color: $second_color;
			width: 90%;
			height: 100%;
			z-index: -1;
			left: auto;
		}

		&::after {
			width: 80%;
			bottom: -40px;
		}

		.compliance-content {
			display: flex;
			justify-content: center;
			align-items: center;
			width: 100%;
			position: relative;
			padding: 50px 70px;

			.compliance-icon {
				margin-inline-end: 15px;
			}

			.compliance-text {
				h3 {
					color: #222222;
					font-weight: 700;
				}
			}
		}
	}
}

