//global style
body {
	font-family: "Heebo", 'Noto Kufi Arabic', sans-serif;
	background-color: #fff;
	// overflow-x: hidden;
	// cursor: url('https://res.cloudinary.com/dvkugaw3b/image/upload/v1641562028/cursor_ad12rf.png'), auto;

	.main-wrapper {
		position: relative;
		min-height: 100vh;
		width: 100%;
		overflow: hidden;
	}
}

.modal-backdrop {
	&.show {
		opacity: .95;
	}
}

#searchModal {
	.modal-content {
		background-color: transparent
	}

	.form-control {
		font-size: 36px;
		border: none;
		border-bottom: 3px solid rgba(255, 255, 255, .1);
		background: 0 0;
		color: #fff;
		padding-inline-start: 0;
		padding-bottom: 12px;
		padding-inline-end: 40px;

		&:focus {
			box-shadow: none
		}
	}

	.btn {
		border-bottom: 3px solid rgba(255, 255, 255, .1);
		color: $second_color;
		font-size: 30px
	}
}

//global style

//header
header {
	transition: all ease 0.3s;
	position: fixed;
	z-index: 999;
	width: 100%;

	&.fixed {
		.main-nav {
			animation-duration: 1s;
			animation-fill-mode: both;
			animation-name: fadeInDown;
			box-shadow: 0 10px 15px rgb(25 25 25 / 10%);
			position: fixed;
			width: 100%;
			top: 0;
			background-color: #fff;

			.navbar-brand {
				img {
					&.dark-logo {
						display: inline-block !important;
					}

					&.white-logo {
						display: none !important;
					}
				}
			}

			.search-icon {
				.btn {
					color: #000;
				}
			}

			.lang {
				.icon {
					span {
						color: #000
					}
				}
			}

			.dropdown-toggle {
				&::before {
					color: #000;
				}
			}
		}
	}

	&.transparent {
		&.fixed {
			.main-nav {
				background-color: #fff;

				.navbar-nav {
					.nav-item {
						.nav-link {
							color: #000;
						}
					}
				}
			}
		}

		.main-nav {
			background-color: transparent;

			.navbar-nav {
				.nav-item {
					&.active {
						.nav-link {
							color: $second_color;
						}
					}

					&:hover {
						.nav-link {
							color: $second_color;
						}
					}

					.nav-link {
						color: #fff;

						&::after {
							background-color: $second_color;
						}
					}
				}
			}

			.navbar-brand {
				img {
					&.dark-logo {
						display: none !important;
					}

					&.white-logo {
						display: inline-block !important;
					}
				}
			}

			.search-icon {
				.btn {
					color: #fff;
				}
			}

			.lang {
				.icon {
					span {
						color: #fff
					}
				}
			}

			.dropdown-toggle {
				&::before {
					color: #fff;
				}
			}
		}

		.hamburger-menu {
			span {
				background: #fff;
			}
		}
	}

	.navbar {
		padding: 5px 0;
		background-color: #fff;

		.navbar-brand {
			padding: 0;
			margin: 0;

			img {
				max-width: 150px;

				&.white-logo {
					display: none !important;
				}
			}
		}

		.navbar-toggler {
			margin-inline-start: 15px;

			&:focus {
				outline: none;
				box-shadow: none
			}
		}

		.navbar-nav {
			padding: 0;

			.nav-item {
				padding: 0 1rem;
				line-height: 5;
				transition: all ease 0.2s;

				&:hover,
				&.active {
					.nav-link {
						color: $second_color;

						&::after {
							width: 100%;
						}
					}
				}

				&:not(:hover) {
					color: $second_color;
				}

				&:last-child {
					padding-inline-end: 0;
				}

				.nav-link {
					color: #223645;
					padding: 0;
					font-size: 15px;
					text-transform: capitalize;
					font-weight: 600;
					position: relative;

					&::after {
						content: "";
						position: absolute;
						background-color: $second_color;
						height: 2px;
						left: 0;
						bottom: 22px;
						width: 0;
						transition: all ease 0.2s;
					}

					&:hover {
						&::after {
							width: 100%;
						}
					}

					&.change {
						&::after {
							right: 0;
							left: auto;
						}
					}
				}

				&.dropdown {
					.dropdown-menu[data-bs-popper] {
						margin: 0;
						min-width: 225px;
					}
				}

				.dropdown-menu {
					padding: 0;
					box-shadow: 0 10px 50px 0 rgba(13, 38, 59, .01);
					border-radius: 0;
					border-top: 2px solid $main_color;
					transition: all ease 0.2s;

					li {
						border-bottom: 1px solid #eee;
						line-height: 3;

						&:last-of-type {
							border-bottom: none;
						}
					}

					.dropdown-item {
						font-size: 13px;
						padding: 0 20px;
						transition: all ease 0.2s;
						text-transform: capitalize;
						position: relative;

						i {
							margin-inline-end: 5px;
						}

						&:hover {
							color: #fff;
							background-color: $main_color;
						}
					}
				}
			}
		}

		.navbar-collapse {
			justify-content: end;
			padding-inline-end: 65px;
		}

		.dropdown-toggle {
			position: relative;
			display: flex;
			align-items: center;
			padding-inline-end: 15px !important;

			&::before {
				content: "\F282";
				font-family: "bootstrap-icons";
				-webkit-font-smoothing: antialiased;
				font-style: normal;
				font-variant: normal;
				font-weight: 600;
				font-size: 10px;
				position: absolute;
				text-transform: initial;
				right: 0;
				top: auto;
				color: #000;
			}

			&::after {
				display: none;
			}
		}

		.group {
			display: flex;
			order: 1;
			align-items: center;
		}

		.search-icon {
			.btn {
				color: #000;
			}
		}

		.lang {
			padding-inline-start: 20px;

			button {
				background-color: transparent;
				border: none;
			}

			.icon {
				img {
					@extend %fitimage;
					width: 25px;
					margin-inline-end: 10px;
				}

				span {
					text-transform: uppercase;
					font-size: 14px;
					font-weight: 600;
					color: #000
				}
			}

			.lang-content {
				border-radius: 0;
				@include boxshadow(0 0 37px rgba(0, 0, 0, .07));
				border: none;
				padding: 0;
				right: 0;
				left: auto;

				a {
					font-size: 14px;
					display: block;
					font-weight: 600;
					text-decoration: none;
					padding: 10px;
					border: 0;
					background-color: #ffffff;
					color: #222222;
					line-height: 1;
					text-transform: capitalize;

					&:hover {
						background-color: #e9e9e9
					}
				}

				img {
					@extend %fitimage;
					width: 18px;
					margin-inline-end: 10px;
				}
			}
		}
	}

	.hamburger-menu {
		display: inline-block;
		margin-inline-start: auto;
		width: 30px;
		height: 20px;
		position: relative;
		-webkit-transform: rotate(0deg);
		-moz-transform: rotate(0deg);
		-o-transform: rotate(0deg);
		transform: rotate(0deg);
		transition-duration: 500ms;
		-webkit-transition-duration: 500ms;
		transition-timing-function: cubic-bezier(0.86, 0, 0.07, 1);
		-webkit-transition-timing-function: cubic-bezier(0.86, 0, 0.07, 1);
		cursor: pointer;
		display: none;

		&:hover {
			span {
				width: 30px !important;
			}
		}

		span {
			display: block;
			position: absolute;
			height: 2px;
			width: 100%;
			background: #000;
			opacity: 1;
			right: 0;
			@include transform(rotate(0deg));
			transition: 0.25s ease-in-out;

			&:nth-child(1) {
				top: 0;
				width: 13px;
			}

			&:nth-child(2) {
				top: 9px;
			}

			&:nth-child(3) {
				top: 18px;
				width: 100%;
			}
		}

		&.open {
			span {
				&:nth-child(1) {
					top: 9px;
					right: 0;
					-webkit-transform: rotate(135deg);
					-moz-transform: rotate(135deg);
					-o-transform: rotate(135deg);
					transform: rotate(135deg);
					width: 28px !important;
				}

				&:nth-child(2) {
					opacity: 0;
					right: 20px;
				}

				&:nth-child(3) {
					top: 9px;
					right: 0;
					-webkit-transform: rotate(-135deg);
					-moz-transform: rotate(-135deg);
					-o-transform: rotate(-135deg);
					transform: rotate(-135deg);
					width: 28px !important;
				}
			}
		}
	}

	.offcanvas {
		.btn-close {
			&:focus {
				outline: none;
				box-shadow: none
			}
		}

		.offcanvas-header {
			justify-content: flex-end;
			background: #fff;
		}

		.navbar {
			position: static;

			.navbar-nav {
				.nav-item {
					line-height: 1.5;
					border-bottom: 1px solid #f5f5f5;

					.dropdown-toggle {
						position: relative;

						&::after {
							position: absolute;
							right: 0;
							vertical-align: middle;
							top: 45%;
						}
					}

					.nav-link {
						padding: 10px 10px;
					}
				}
			}
		}
	}
}

//header

//main slider
.main-slider {
	padding: 300px 0 0;
	background-position: bottom;
	background-size: cover;
	position: relative;
	z-index: 1;

	&::after {
		content: "";
		position: absolute;
		height: 100%;
		width: 100%;
		left: 0;
		top: 0;
		background-color: $main_color;
		opacity: 0.95;
		z-index: -1;
	}

	.content {
		text-align: center;
		overflow: hidden;

		.text {
			color: #fff;

			h1 {
				font-size: 18px;
				font-weight: 600;
				color: #fff;
				text-transform: uppercase;
				display: inline-block;
				background: $second_color;
				position: relative;
				line-height: 1;
				padding: 16px 50px;
				margin-bottom: 20px;

				&::before,
				&::after {
					content: "";
					position: absolute;
					top: 0;
					width: 0;
					height: 0;
					border-style: solid;
				}

				&::before {
					left: -25px;
					border-width: 0 0 50px 25px;
					border-color: transparent transparent $second_color transparent;
				}

				&::after {
					right: -25px;
					border-width: 50px 25px 0 0;
					border-color: $second_color transparent transparent transparent;
				}

				.dot {
					height: 10px;
					width: 10px;
					background: #fff;
					border-radius: 50%;
					position: absolute;
					top: 50%;
					transform: translateY(-50%);
				}

				.dot1 {
					left: 10px;
				}

				.dot2 {
					right: 10px;
				}
			}

			h2 {
				font-size: 70px;
				font-weight: 800;
				text-transform: uppercase;
				@extend %max_lines;
				-webkit-line-clamp: 3;
			}

			.owl-item {
				&.active {
					animation-duration: 2s;
					animation-fill-mode: both;
				}

				&.cloned {
					animation-duration: 2s;
					animation-fill-mode: both;
				}
			}
		}

		.slider {
			margin-bottom: 30px;
		}

		.mouse-arrow {
			width: 50px;
			max-height: 75px;
			border: 3px solid #b1a8c0;
			border-radius: 22px;
			cursor: pointer;
			color: #fff;
			padding: 10px 0 25px;
			margin: 0 auto 50px;
			position: relative;

			.mouse-dots {
				writing-mode: tb-rl;
				font-size: 30px;
				line-height: 17px;
				letter-spacing: -2px;
				color: #b1a8c0;
				position: absolute;
				left: 50%;
			}

			.arrow {
				font-weight: 700;
				color: $second_color;
				font-size: 26px;
				line-height: 1;
				animation: 1.5s scroll cubic-bezier(0.65, -0.55, 0.25, 1.5) infinite;
				top: 10px;
				position: relative;
			}
		}

		.images {
			position: relative;
			max-height: 350px;
			// overflow: hidden;

			.img1,
			.img2 {
				position: absolute;
				z-index: 1;
			}

			.img1 {
				// left: 10%;
				// top: 19.5%;
				transform: rotate(-10deg);
				left: 7%;
				top: 10%;
			}

			.img2 {
				right: 7%;
				top: 15.5%;
				transform: rotate(71deg) rotateY(180deg);
			}

			.main-image {
				animation: spin 30s linear infinite;
				position: relative;
			}
		}
	}
}

@keyframes spin {
	0% {
		transform: rotate(0deg);
	}

	100% {
		transform: rotate(-360deg);
	}
}

@keyframes scroll {
	0% {
		opacity: 0;
		transform: translateY(0) scale(0.5);
	}

	10% {
		opacity: 1;
		transform: scale(1);
	}

	100% {
		opacity: 0;
		transform: translateY(30px) scale(0.5);
	}
}

