//logistics
section.logistics {
	padding: 110px 0;
	position: relative;
	background-color: #fcfcfc;
	z-index: 0;
	background-position: bottom;
	background-size: cover;

	.content {
		.nav {
			justify-content: space-between;
			// flex-direction: column;
			// width: 270px;
			// min-width: 270px;
			// margin-inline-end: 50px;

			.nav-item {
				margin: 0;
				width: calc((100% / 3) - 25px);
				margin-bottom: 85px;

				&:last-child {
					margin-bottom: 0;
				}
			}

			.nav-link {
				width: 100%;
				border-radius: 0;

				&.active {
					border-color: $second_color;

					&::before {
						transform: rotate(35deg) scaleY(0.75);
					}
				}
			}
		}

		.tab-content {
			.image {
				width: 407px;
				max-width: 100%;
				height: 100%;

				img {
					@extend %fitimage;
				}
			}

			.text {
				display: flex;
				justify-content: center;
				flex-direction: column;
				height: 100%;

				h3 {
					font-size: 28px;
					line-height: 50px;
					margin-bottom: 10px;
					text-transform: uppercase;
					color: $main_color;
					position: relative;
				}

				p {
					color: $gray;
				}

				ul {
					padding: 0;

					li {
						width: 49%;
						display: inline-flex;
						align-items: baseline;
						position: relative;
						margin-bottom: 10px;
						color: $gray;

						&::before {
							content: "";
							background-color: $second_color;
							width: 7px;
							min-width: 7px;
							height: 7px;
							display: inline-block;
							margin-inline-end: 10px;
							border-radius: 50%;
						}
					}
				}
			}

			.progress-logistics {
				.progress-box {
					&:last-child {
						.progress {
							margin-bottom: 0;
						}
					}

					.progress-title {
						font-size: 18px;
						font-weight: 700;
						color: $main_color;
						text-transform: uppercase;
						margin: 0 0 20px;

						i {
							margin-inline-end: 10px;
							font-size: 20px;
						}
					}

					.progress {
						height: 2px;
						background: $main_color;
						border-radius: 0;
						margin-bottom: 45px;
						overflow: visible;
						box-shadow: none;

						.progress-bar {
							background: #fff;
							box-shadow: none;
							border-top: 4px solid $second_color;
							border-radius: 0;
							position: relative;
							top: -7px;
							animation: animate-positive 2s;
							overflow: visible;

							&::before {
								content: "";
								position: absolute;
								bottom: 0;
								right: 0;
								height: 35px;
								width: 12px;
								background-color: $second_color;
							}

							.progress-value {
								font-size: 17px;
								font-weight: 700;
								color: #000;
								position: absolute;
								top: -33px;
								right: 0;
								visibility: hidden;
							}
						}
					}
				}


				@keyframes animate-positive {
					0% {
						width: 0;
					}
				}
			}
		}

		.single-logistics {
			display: block;
			text-align: center;
			border: 8px solid $main_color;
			padding: 20px 30px;
			background: $main_color;
			position: relative;
			text-decoration: none;

			&:hover {
				&::before {
					transform: rotate(35deg) scaleY(0.75);
				}
			}

			&::before {
				content: "";
				position: absolute;
				height: 230%;
				width: 22px;
				background: $second_color;
				transform: rotate(35deg) scaleY(0.5);
				top: -65%;
				right: 45%;
				z-index: -1;
				transition: .4s linear;
			}

			.logistics-icon {
				margin-bottom: 25px;

				i {
					font-size: 52px;
					line-height: 1;
					color: $second_color;
				}
			}

			.logistics-content {
				h2 {
					color: #fff;
					font-size: 18px;
					margin-bottom: 0;
				}
			}
		}
	}
}

