//about
section.about {
	padding: $padding_section;
	background-color: #fafafa;

	.content {
		.image-box {
			position: relative;
			height: 100%;

			img {
				@extend %fitimage;
			}
		}

		.text-box {
			position: relative;

			h2 {
				font-size: 36px;
				font-weight: 700;
				text-transform: uppercase;
				margin-bottom: 15px;
				color: $main_color;
				position: relative;
			}

			p {
				color: $gray;
				@extend %max_lines;
				-webkit-line-clamp: 4;
			}

			.btn {
				@extend %btn_selector;
			}
		}
	}

}

//about

//video
section.video-area {
	.video-overlay {
		position: relative;
		z-index: 1;

		&::before {
			content: "";
			position: absolute;
			left: 0;
			top: 0;
			width: 880px;
			max-width: 100%;
			background: rgba(227, 160, 55, .8);
			height: 100%;
			z-index: -1;
		}
	}

	.video-play {
		text-align: center;

		a {
			display: inline-block;
			position: relative;

			&:hover {
				&::before {
					width: 78px;
				}
			}

			&::before {
				content: "";
				position: absolute;
				right: 73px;
				top: 50%;
				transform: translateY(-50%);
				width: 0;
				height: 2px;
				background: #fff;
				transition: .4s linear;
			}
		}
	}

	.video-title {
		padding: 150px 0;
		padding-inline-start: 40px;

		>span {
			font-size: 18px;
			font-weight: 900;
			color: $second_color;
			padding: 11px 15px;
			background: #f9f9f9;
			line-height: 1;
			display: inline-block;
			margin-inline-start: 10px;
			margin-bottom: 20px;
			position: relative;

			&::before {
				content: "";
				position: absolute;
				left: -10px;
				top: 0;
				width: 0;
				height: 0;
				border-style: solid;
				border-width: 0 0 40px 10px;
				border-color: transparent transparent #fff transparent;
			}

			&::after {
				content: "";
				position: absolute;
				right: -10px;
				top: 0;
				width: 0;
				height: 0;
				border-style: solid;
				border-width: 40px 10px 0 0;
				border-color: #fff transparent transparent transparent;
			}
		}

		h2 {
			font-size: 70px;
			text-transform: uppercase;
			color: #fff;
			line-height: 1;
			margin-bottom: 35px;
		}

		a {
			font-size: 14px;
			text-transform: uppercase;
			color: #fff;
			font-weight: 700;
			display: inline-block;
			position: relative;

			&:hover {
				span {
					&::before {
						width: 42px;
					}
				}
			}

			>span {
				// display: inline-block;
				// width: 0;
