//features
section.features {
	padding: $padding_section;
	position: relative;
	z-index: 0;
	background-position: bottom;
	background-size: cover;

	.content {
		display: grid;
		grid-template-columns: repeat(3, calc(100% / 3));
		// grid-template-columns: 35% 30% 35%;

		.features-box {
			display: flex;
			margin-bottom: 30px;

			&:hover {
				.icon {
					background-color: $second_color;

					i {
						color: #fff;
					}
				}

				.details {
					.title {
						color: $second_color;
					}
				}
			}

			.icon {
				display: flex;
				align-items: center;
				justify-content: center;
				border-radius: 50%;
				height: 75px;
				width: 75px;
				min-width: 75px;
				border: 1px dashed $second_color;
				margin-inline-end: 15px;
				transition: all ease 0.3s;

				i {
					transition: all ease 0.3s;
					color: $second_color;
					font-size: 28px;
				}
			}

			.details {
				.title {
					font-size: 18px;
					text-transform: uppercase;
					color: #2c2b2e;
					margin-bottom: 10px;
					font-weight: 700;
					@extend %max_lines;
					-webkit-line-clamp: 2;
					transition: all ease 0.3s;
				}

				p {
					font-size: 16px;
					line-height: 26px;
					color: #656565;
					@extend %max_lines;
					-webkit-line-clamp: 2;
				}
			}
		}

		.some-features {
			padding-inline-start: 50px;

			&:nth-child(1) {
				padding-inline-start: 0;
				padding-inline-end: 50px;

				.features-box {
					flex-direction: row-reverse;
					text-align: end;

					.icon {
						margin-inline-start: 15px;
						margin-inline-end: 0;
					}
				}
			}
		}

		.features-image {
			position: relative;

			&::before {
				content: "";
				position: absolute;
				background-image: url(../images/features/ellips_shape.png);
				width: 550px;
				height: 550px;
				background-repeat: no-repeat;
				left: 0;
				top: -30px;
				z-index: -1;
			}
		}
	}
}

//features

//contact
section.contact {
	padding: 80px 0 50px 0;
	background-color: #fff;
	background-image: url(../images/bgs/contact_bg.png);
	background-size: cover;

	.content {
		background-color: #fff;
		@include boxshadow(0 20px 40px rgb(0 0 0 / 10%));

		.image {
			height: 100%;

			img {
				@extend %fitimage;
			}
		}

		.contact-form {
			padding: 40px;
			padding-inline-start: 20px;

			.main-title {
				text-align: start;
				margin-bottom: 2rem;

				h2 {
					&::after {
						display: none;
					}

					&::before {
						display: none;
					}
				}
			}

			.form-group {
				margin-bottom: 20px;

				label {
					display: block;
					margin-bottom: 15px;
					font-weight: 600;
					text-transform: capitalize;
					font-size: 14px;
				}

				.form-control,
				.form-select {
					display: block;
					width: 100%;
					line-height: 28px;
					padding: 12px 15px;
					color: #222222;
					font-size: 14px;
					font-weight: 400;
					border-radius: 0px;
					background-color: #f8f8f8;
					border-color: #f8f8f8;

					&:focus {
						border-color: $dark_color;
						box-shadow: none;
					}
				}

				textarea {
					height: auto !important;
				}
			}
		}

		.contact-btn {
			.btn {
				@extend %btn_selector;
			}
		}

		.successfully {
			font-size: 16px;
			margin-bottom: 25px;
			color: #058100;
		}
	}
}

//contact

//partners
section.partners {
	padding: $padding_section;
	background-color: #fff;

	.content {
		.item {
			&:hover {
				cursor: move;

				img {
					opacity: 1;
					-webkit-filter: grayscale(0%);
					filter: grayscale(0%);
				}
			}

			img {
				opacity: 0.5;
				-webkit-filter: grayscale(100%);
				filter: grayscale(100%);
				transition: all ease 0.3s;
				object-fit: contain;
				-o-object-fit: contain;
				height: 100px;
			}
		}
	}
}

//partners

//footer
footer {
	padding: 60px 0 0;
	background-position: center;
	background-size: cover;
	background-image: url(../images/bgs/footer_bg.jpg);

	.row {
		row-gap: 20px;
	}

	.footer-content {
		padding-bottom: 30px;

		.footer-box {
			.text {
				p {
					color: #969696;
					font-size: 14px;
					line-height: 1.7em;
					@extend %max_lines;
					// -webkit-line-clamp: 5;
				}

				.footer-logo {
					img {
						max-width: 170px;
					}
				}

				.btn {
					padding-inline-start: 0;
					padding-top: 0;
					color: $main_color;
					font-weight: 600;

					img {
						width: 7px;
						margin-inline-start: 5px;
					}
				}
			}

			.social-links {
				.links {
					display: flex;

					a {
						display: block;
						height: 40px;
						width: 40px;
						text-align: center;
						line-height: 40px;
						color: #949697;
						background: #252526;
						border-radius: 50%;
						font-size: 14px;
						margin-inline-end: 15px;
						transition: all ease 0.3s;

						&:hover {
							color: #fff;
							opacity: 1;
						}
					}
				}
			}

			h4 {
				font-size: 18px;
				position: relative;
				font-weight: 600;
				color: #fff;
				opacity: .75;
				line-height: 1.2em;
				margin-bottom: 25px;
				text-transform: capitalize;

				&::before {
					content: "";
					height: 16px;
					width: 3px;
					background-color: $second_color;
					display: inline-block;
					margin-inline-end: 10px;
				}
			}

			.map-links {
				padding: 0;

				li {
					list-style: none;
					margin-bottom: 10px;
					font-size: 14px;
					padding-inline-start: 20px;
					position: relative;

					&::before {
						font-family: "bootstrap-icons";
						content: "\F285";
						-webkit-font-smoothing: antialiased;
						font-style: normal;
						font-variant: normal;
						font-weight: 500;
						font-size: 12px;
						position: absolute;
						left: 0;
						top: 50%;
						transform: translateY(-50%);
						color: #969696;
					}

					a {
						color: #969696;
						text-transform: capitalize;
						@extend %max_lines;
						-webkit-line-clamp: 1;
						transition: all ease 0.3s;

						&:hover {
							color: #fff;
						}

						&.btn {
							padding-inline-start: 0;
							padding-top: 0;
							color: $main_color;
							font-weight: 600;
						}
					}
				}
			}

			.contact {
				p {
					list-style: none;
					margin-bottom: 10px;
					font-size: 14px;
					color: #969696;
					margin-bottom: 20px;
					display: flex;

					i {
						margin-inline-end: 10px;
						color: $second_color;
					}
				}

				a {
					color: #969696;
					display: inline-block;
					// direction: ltr;
					transition: all ease 0.3s;

					&:hover {
						color: #fff;
						text-decoration: underline;
					}
				}
			}
		}
	}

	.copyright {
		background-color: #0c0b0d;
		padding: 30px 0;
		text-align: center;
		// border-top: 1px solid rgba(255, 255, 255, 0.15);

		.copy-text {
			p {
				margin-bottom: 0;
				color: #777676;
				font-size: 16px;
				margin: 0;
				text-transform: capitalize;

				a {
					color: #fff;
					margin-inline-start: 5px;
				}
			}
		}
	}
}

