@media screen and (max-width: 1399px) {
	.main-slider {
		.content {
			.images {
				.img1 {
					left: 3%;
					// top: 21.5%;
					// transform: rotate(-14deg);
				}

				.img2 {
					right: 3%;
					transform: rotate(74deg) rotateY(180deg);
				}
			}
		}
	}
}

@media screen and (max-width: 1199px) {
	.main-slider {
		.content {
			.images {
				.img1 {
					// top: 10.5%;
					// transform: rotate(-11deg);

					left: -1.5%;
				}

				.img2 {
					right: -1.5%;
					transform: rotate(78deg) rotateY(180deg);
				}
			}
		}
	}

	section.features {
		.content {
			grid-template-columns: repeat(2, 50%);

			.some-features {
				padding-inline-start: 20px;

				&:nth-child(1) {
					padding-inline-end: 20px;
				}
			}

			.features-image {
				display: none;
			}
		}
	}
}

@media screen and (max-width: 991px) {
	header {
		.navbar {
			.navbar-brand {
				img {
					max-width: 120px;
				}
			}

			.navbar-toggler {
				order: 1;
			}

			.navbar-nav {
				.nav-item {
					.nav-link {
						&::after {
							display: none;
						}
					}
				}
			}
		}

		.hamburger-menu {
			display: inline-block;
		}
	}

	.main-slider {
		.content {
			.text {
				h1 {

					&::after,
					&::before {
						display: none;
					}
				}

				h2 {
					font-size: 46px;
				}
			}

			.images {
				max-height: 250px;

				.img1 {
					top: -6.5%;
					transform: rotate(-9deg);
				}

				.img2 {
					right: 1%;
					transform: rotate(72deg) rotateY(180deg);
					top: 0;
					max-width: 180px;
				}
			}
		}
	}

	section.about {
		.content {
			.text-box {
				text-align: center;
				margin-bottom: 40px;
			}
		}
	}

	section.video-area {
		.video-title {
			padding: 0 20px 90px;
			text-align: center;

			h2 {
				font-size: 50px;
			}
		}

		.video-overlay {
			padding-top: 90px;
		}

		.video-play {
			margin-bottom: 50px;
		}
	}

	section.logistics {
		.content {
			.single-logistics {
				border-width: 5px;
				padding: 20px;
			}

			.tab-content {
				.image {
					width: 100%;
					height: auto;
					max-height: 350px;
					overflow: hidden;
					margin-bottom: 25px;
				}
			}
		}
	}

	section.contact {
		.content {
			.contact-form {
				padding: 20px;
			}
		}
	}

	section.about-in-page {
		.content {
			.tab-content {
				padding: 20px;
			}

			.nav {
				margin-inline-end: 0;

				.nav-link {
					border: 1px solid transparent;
					margin-bottom: 15px;

					&.active {
						border-color: $second_color;

						&::before {
							display: none;
						}
					}
				}
			}
		}
	}

	section.tracking {
		.content {
			.tracking-box {
				margin-bottom: 25px;
			}
		}
	}

	.side-widget {
		padding-inline-start: 0;
		margin-top: 15px;
	}

	footer {
		.footer-box {
			margin-bottom: 25px;
		}
	}
}

@media screen and (max-width: 767px) {
	.main-slider {
		padding-top: 240px;

		.content {
			.text {
				h1 {
					padding: 10px 25px;
				}

				h2 {
					font-size: 26px;
				}
			}

			.images {
				display: none;
			}
		}
	}

	section.features {
		.content {
			grid-template-columns: repeat(1, 100%);

			.some-features {
				padding-inline-start: 0;

				&:nth-child(1) {
					padding-inline-end: 0;

					.features-box {
						flex-direction: row;
						text-align: start;

						.icon {
							margin-inline-start: 0;
							margin-inline-end: 15px
						}
					}
				}
			}
		}
	}

	section.video-area {
		.video-title {
			h2 {
				font-size: 36px;
			}
		}
	}

	section.logistics {
		.content {
			.single-logistics {
				padding: 20px 10px;

				.logistics-content {
					h2 {
						font-size: 16px;
					}
				}

				.logistics-icon {
					margin-bottom: 15px;

					i {
						font-size: 36px;
					}
				}

				&::before {
					display: none;
				}
			}

			.nav {
				.nav-item {
					width: calc((100% / 3) - 5px);
					margin-bottom: 35px;
				}
			}
		}
	}
}

@media screen and (max-width: 575px) {
	header {
		.navbar {
			.lang {
				padding-inline-start: 5px;
			}
		}
	}

	section.about {
		.content {
			.text-box {
				h2 {
					font-size: 24px;
				}
			}
		}
	}

	section.logistics {
		.content {
			.tab-content {
				.progress-logistics {
					.progress-box {
						.progress-title {
							font-size: 16px;
							margin-bottom: 25px;

							i {
								font-size: 18px;
								margin-inline-end: 5px;
							}
						}

						.progress {
							.progress-bar {
								&::before {
									height: 20px;
									width: 12px;
								}
							}
						}
					}
				}
			}
		}
	}

	section.features {
		.content {
			.some-features {
				&:nth-child(1) {
					.features-box {
						flex-direction: column;
						text-align: center;
						align-items: center;

						.icon {
							margin-inline-end: 0;
							margin-bottom: 15px;
						}
					}
				}

				.features-box {
					flex-direction: column;
					text-align: center;
					align-items: center;

					.icon {
						margin-inline-end: 0;
						margin-bottom: 15px;
					}
				}
			}
		}
	}

	section.logistics {
		.content {
			.single-logistics {
				padding: 20px 10px;

				.logistics-content {
					h2 {
						font-size: 16px;
					}
				}

				.logistics-icon {
					margin-bottom: 10px;

					i {
						font-size: 28px;
					}
				}

				&::before {
					display: none;
				}
			}

			.nav {
				display: block;
				margin-bottom: 50px;

				.nav-item {
					width: 100%;
					margin-bottom: 10px;
				}
			}
		}
	}

	.main-breadcrumb {
		padding: calc(5rem + 85px) 0 5rem;

		.breadcrumb-title {
			h2 {
				font-size: 30px;
				line-height: 30px;
			}
		}
	}

	section.compliance-policy {
		.content {
			.compliance-content {
				flex-direction: column;
				text-align: center;
				padding: 25px;

				.compliance-icon {
					margin-inline-end: 0;
					margin-bottom: 15px;
				}
			}
		}
	}

	section.request-quote {
		.content {
			.stepwizard {
				.stepwizard-step {
					p {
						font-size: 12px;
						line-height: normal;
					}

					.btn-rounded {
						height: 80px;
						width: 80px;
						font-size: 28px;
					}
				}
			}
		}
	}

	section.tracking {
		.content {
			.tracking-box {
				.tracking-icon {
					height: 250px;

					img {
						border-radius: 60px;
					}
				}
			}
		}
	}

	section.search-result {
		.content {
			.search-title {
				text-align: center;

				h3 {
					font-size: 24px;
					margin-bottom: 25px;

					span {
						display: block;
					}
				}
			}

			.search-box {
				padding: 20px;
				text-align: center;
			}

			.result-title {
				a {
					h4 {
						margin-bottom: 20px;
						font-size: 22px;
					}
				}
			}
		}
	}

	section.error {
		.content {
			h3 {
				font-size: 26px;
				color: #0b426b;
			}
		}
	}
}

@media screen and (max-width: 400px) {
	header {
		.navbar {
			.navbar-brand {
				img {
					max-width: 100px;
				}
			}
		}
	}
}