//global variables 
$main_color: #0B426B;
$second_color: #E3A037;
$dark_color: #252525;
$gray: #7a8a9e;
$padding_section: 85px 0;
//global styles

//mixins
@mixin borderRadius($border) {
	border-radius: $border;
	-webkit-border-radius: $border;
	-moz-border-radius: $border;
	-ms-border-radius: $border;
	-o-border-radius: $border;
}

@mixin transition($trans) {
	transition: $trans;
	-webkit-transition: $trans;
	-moz-transition: $trans;
	-ms-transition: $trans;
	-o-transition: $trans;
}

@mixin transform($transform) {
	transform: $transform;
	-webkit-transform: $transform;
	-moz-transform: $transform;
	-ms-transform: $transform;
	-o-transform: $transform;
}

@mixin animation($anim) {
	animation: $anim;
	-webkit-animation: $anim;
	-moz-animation: $anim;
	-ms-animation: $anim;
	-o-animation: $anim;
}

@mixin animationDelay($anim) {
	-webkit-animation-delay: $anim;
	-moz-animation-delay: $anim;
	-ms-animation-delay: $anim;
	-o-animation-delay: $anim;
	animation-delay: $anim;
}

@mixin boxshadow($shadow) {
	box-shadow: $shadow;
	-webkit-box-shadow: $shadow;
	-moz-box-shadow: $shadow;
	-ms-box-shadow: $shadow;
	-o-box-shadow: $shadow;
}

//mixins

//global btn
%btn-item {
	@include borderRadius(0);
	font-size: 15px;
	color: $main_color;
	text-transform: capitalize;
	@include transition(all ease 0.3s);
	padding: 12px 20px;
	color: #fff;
	position: relative;
	background-color: $main_color;
	overflow: hidden;

	&:focus,
	&:active,
	&:hover {
		background-color: $dark_color;
	}

	&.diff {
		background-color: transparent;
		color: #000;
		text-decoration: underline;
		border: none;
	}
}

//global btn

//global styles
.btn,
.btn-close {
	&:focus {
		box-shadow: none;
		outline: none;
	}
}

dl,
ol,
ul {
	padding: 0;
	padding-inline-start: 20px;
}

.modal-backdrop.show {
	opacity: .7;
}

a {
	text-decoration: none;
}

.form-check-input:checked {
	background-color: $main_color;
	border-color: $main_color;
}

.alert {
	font-size: 14px;
	@include borderRadius(0);

	strong {
		margin-inline-end: 5px;
	}
}

.alert-dismissible .btn-close {
	left: auto;
}

//global styles


//placeholder selectors
%center {
	display: flex;
	align-items: center;
	justify-content: center;
}

%overflow_text {
	overflow: hidden;
	text-overflow: ellipsis;
	display: -webkit-box;
	-webkit-box-orient: vertical;
}

%overlay {
	content: "";
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
}

%before {
	content: "";
	position: absolute;
}

%fitimage {
	width: 100%;
	height: 100%;
	-o-object-fit: cover;
	object-fit: cover;
}

%max_lines {
	overflow: hidden;
	text-overflow: ellipsis;
	-webkit-box-orient: vertical;
	display: -webkit-box;
}

%max_one_line {
	max-width: 100%;
	text-overflow: ellipsis;
	white-space: nowrap;
	overflow: hidden;
}

%btn_selector {
	background: $second_color none repeat scroll 0 0;
	border: medium none;
	border-radius: 0;
	color: #fff;
	display: inline-block;
	font-size: 14px;
	font-weight: 800;
	margin-bottom: 0;
	padding: 16px 30px;
	text-align: center;
	text-transform: uppercase;
	touch-action: manipulation;
	transition: all 0.4s cubic-bezier(0.215, 0.61, 0.355, 1) 0s;
	vertical-align: middle;
	white-space: nowrap;
	position: relative;
	z-index: 1;

	&::before {
		content: '';
		display: block;
		position: absolute;
		left: 0;
		bottom: 0;
		height: 0;
		width: 100%;
		z-index: -1;
		background: $main_color;
		transition: all 0.4s cubic-bezier(0.215, 0.61, 0.355, 1) 0s;
	}

	&:hover {
		&::before {
			top: 0%;
			bottom: auto;
			height: 100%;
		}
	}
}

//placeholder selectors

//toast
.invalid-tooltip,
.invalid-feedback {
    display: block
}

.toast-part {
   //  z-index: 999;
   //  left: 0;
   //  top: 0;
   //  width: 100%;
	margin-bottom: 25px;

    .toast-content {
        justify-content: space-between;
        align-items: center;
    }

    .toast {
        border-radius: 0;
        width: 100%;
        text-transform: capitalize;

        .btn-close {
            margin: 15px;
        }
    }
}

//toast

//main title
.main-title {
	margin-bottom: 4rem;
	position: relative;
	text-align: center;

	h2 {
		font-size: 36px;
		color: #18364a;
		position: relative;
		text-transform: uppercase;
		font-weight: 800;
		margin-bottom: 30px;
		padding-bottom: 15px;

		&::before {
			content: "";
			position: absolute;
			left: 0;
			right: 0;
			width: 80px;
			height: 1px;
			background: $second_color;
			bottom: 1px;
			margin: 0 auto;
		}

		&::after {
			content: "";
			position: absolute;
			left: 0;
			right: 0;
			width: 40px;
			height: 3px;
			background: $second_color;
			bottom: 0px;
			margin: 0 auto;
		}
	}

	p {
		max-width: 770px;
		margin: auto;
		line-height: 1.8;
		color: $gray;
	}
}

//main title

//to top button
#toTop {
	position: fixed;
	right: 15px;
	bottom: 15px;
	margin: 0;
	z-index: 99;
	display: none;

	button {
		background-color: $second_color;
		width: 40px;
		height: 40px;
		border: none;
		color: #fff;
		border-radius: 5px;
	}
}

//to top button

//main broadcrumb
.main-breadcrumb {
	padding: 1.5rem 0;

	ol {
		margin: 0;
		padding: 0;
	}

	.breadcrumb {
		.breadcrumb-item {
			font-size: 14px;
			text-transform: capitalize;

			&.active {
				color: #6c6b6b;
				font-weight: 500;
			}

			&::before {
				color: #6c6b6b;
			}
		}
	}
}

//main broadcrumb

//loading
.ctn-preloader {
	align-items: center;
	cursor: default;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	height: 100%;
	-webkit-box-pack: center;
	-ms-flex-pack: center;
	justify-content: center;
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	z-index: 9000;
	background-color: #fff;

	.animation-preloader {
		.spinner {
			animation: spinner 1s infinite linear;
			border-radius: 50%;
			border: 3px solid rgba(0, 0, 0, 0.2);
			border-top-color: $second_color;
			height: 100px;
			width: 100px;
		}
	}
}

@keyframes spinner {
	to {
	  transform: rotateZ(360deg);
	}
 }

//loading

::-moz-selection {
	color: #fff;
	background: $main_color;
 }
 
 ::selection {
	color: #fff;
	background: $main_color;
 }