html[dir=rtl] {
	body {
		font-family: 'Noto Kufi Arabic', sans-serif;
	}

	.breadcrumb-item+.breadcrumb-item::before {
		float: right;
		padding-left: 5px
	}

	header {
		.navbar {
			.lang {
				.lang-content {
					text-align: right;
				}
			}

			.dropdown-toggle {
				&::before {
					right: auto;
					left: 0
				}
			}

			.navbar-nav {
				.nav-link {
					&::before {
						right: auto;
						left: 0;
					}
				}

				.nav-item {
					.dropdown-menu {
						li {
							text-align: initial;
						}
					}
				}
			}
		}

		.offcanvas {
			.navbar {
				.navbar-nav {
					.nav-item {
						.dropdown-toggle {
							&::after {
								left: 0;
								right: auto
							}
						}
					}
				}
			}

			.offcanvas-header {
				justify-content: flex-start;
			}
		}

		.dropdown-menu[data-bs-popper] {
			right: auto;
			left: 0
		}
	}

	.main-slider {
		.content {
			.text {
				h1 {
					line-height: 1.5;
					padding: 11.5px 50px;
				}

				h2 {
					font-size: 40px;
					line-height: 1.5;
				}
			}
		}
	}

	section.logistics {
		.content {
			.tab-content {
				.progress-logistics {
					.progress-box {
						.progress {
							.progress-bar {
								&::before {
									right: auto;
									left: 0;
								}
							}
						}
					}
				}
			}
		}
	}

	section.about-in-page {
		.content {
			.nav {
				.nav-link {
					&.active {
						&::before {
							right: auto;
							left: -12px;
							border-width: 12px 12px 12px 0;
							border-color: transparent #fff transparent transparent;
						}
					}
				}
			}
		}
	}

	footer {
		.footer-content {
			.footer-box {
				.map-links {
					li {
						&::before {
							right: 0;
							left: auto;
							@include transform(rotate(180deg) translateY(50%));
						}
					}
				}
			}
		}
	}

	@media screen and (max-width: 767px) {
		.main-slider {
			.content {
				.text {
					h2 {
						font-size: 16px;
						line-height: 1.5;
					}
				}
			}
		}
	}
	@media screen and (max-width: 991px) {
		.main-slider {
			.content {
				.text {
					h2 {
						font-size: 26px;
					}
				}
			}
		}
	}
}